  border: 1px solid #CCC;
  padding: 7px;
  display: block;
  overflow: auto;
  font-family: "Courier New", monospace;
  line-height: 1.5em
}

code {
  background: #F4F4F4;
  border: 1px solid #CCC;
  padding: 0 5px
}

blockquote {
  padding: 0 4px 0 20px;
  margin: 10px 0;
  border-left: 4px solid #DDD;
