  margin-top: 10px
}

abbr,
acronym {
  cursor: help;
  border-bottom: 1px dashed #000
}

img {
  vertical-align: middle
}

.break {
  clear: both
}

.clearfix:after {
  content: '.';
  display: block;
  clear: both;
  visibility: hidden;
  height: 0;
  line-height: 0
}

label {
  cursor: pointer
}

fieldset {
  padding: 5px 10px;
  margin: 5px 0;
